.home-header{
    width: 100%;
    height: 30%;
    background-color: #0d6efd;
}

.home-title-header{
    color: white;
}

.carousel{
    -webkit-box-shadow: -10px 0px 13px -7px #000000,
    10px 0px 13px -7px #000000,
    5px 5px 15px 5px rgba(0, 0, 0, 0);
    box-shadow: -10px 0px 13px -7px #000000,
    10px 0px 13px -7px #000000,
    5px 5px 15px 5px rgba(0, 0, 0, 0);
}

.carousel, .carousel-inner > .carousel-item > img {
    
    width: 100%;
    max-width: 820px;
    border-radius: 8px;
    margin: 0 auto;
  }

.carousel-caption{
    background-color: rgba(0, 0, 0, .5);
}

.reward-card-img{
    width: 50%;
    height: 180px;
    
} 
.crew-reward-img{
    width: 100%;
    height: 180px;
    object-fit: cover;
}

.crew-rewards-content{
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    flex-wrap: wrap;
    align-items: center;
}

.card-img-overlay{
    background-color: rgba(0, 0, 0, .5);
    font-family: 'Boogaloo', cursive;
    font-weight: bold;
    text-transform: uppercase;
}

.curent-map{
    width: 80%;
    display: block;
    margin: 0 auto;
}

@media (max-width: 992px) {
    .curent-map{
        width: 100%;
    }   

    .ft-crew-title, .current-map-section-title{
        font-size: 1.8rem;
    }

}

@media only screen and (max-width: 600px) {
    .carousel-title, .carousel-body{
        font-size: 12px;
    }

}